export default "/**\n * overlay-message-outside\n */\n\n.overlay-message-outside {\n  position: fixed;\n  z-index: @index-overlay !important;\n  top: 50%;\n  width: calc(100vw - 510px);\n  text-align: center;\n  @media @max-sm {\n    display: none;\n  }\n}\n\n.overlay-position-left {\n  .overlay-message-outside {\n    right: 0;\n    opacity: 0;\n  }\n  &.in {\n    .overlay-message-outside {\n      .anim-x-inverse.in();\n    }\n  }\n  &.out {\n    .overlay-message-outside {\n      .anim-x.out();\n    }\n  }\n}\n\n.overlay-position-right {\n  .overlay-message-outside {\n    left: 0;\n    opacity: 0;\n  }\n  &.in {\n    .overlay-message-outside {\n      .anim-x.in();\n    }\n  }\n  &.out {\n    .overlay-message-outside {\n      .anim-x-inverse.out();\n    }\n  }\n}";