export default "#iframe--furniture-hero-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    @import 'xtend-library/src/addons/slider/navigation-sides.less';\n\n    /**\n      * slider\n      */\n\n    .slider {\n      position: relative;\n      background: @shade-100;\n      .slides-inner {\n        .row-space(none);\n        // dragger animations\n        .transition-none() !important;\n        .transform-none() !important;\n      }\n      .slide {\n        position: relative;\n        .col(12);\n        // activation\n        display: none;\n        &.active, &.out {\n          display: block;\n          z-index: @index-active;\n        }\n        &.next {\n          display: block;\n          z-index: @index-active + 1;\n        }\n        &:not(.active) {\n          position: absolute;\n        }\n      }\n      .slider-pagination {\n        display: none;\n      }\n      // .hero\n      .hero {\n        .full();\n        display: block;\n        overflow: hidden;\n      }\n      .hero_asset {\n        .media-container {\n          height: 100vh;\n          height: calc(var(--vh, 1vh) * 100);\n        }\n        &:before {\n          content: '';\n          position: absolute;\n          z-index: 2;\n          left: 0;\n          bottom: 0;\n          right: 0;\n          height: 75%;\n          background: linear-gradient(180deg, fade(@black, 0%) 0%, fade(@black, 30%) 100%);\n        }\n      }\n      .hero_content_outer {\n        position: absolute;\n        z-index: 3;\n        top: 0;\n        left: 0;\n        bottom: 0;\n        right: 0;\n        display: flex;\n        align-items: flex-end;\n      }\n      .hero_content {\n        .container();\n      }\n      .hero_content_inner {\n        .full();\n        .text-inverse();\n        .padding(y, giant);\n        .padding(right, big);\n        max-width: 70rem;\n      }\n      .hero_text {\n        font-size: 1.7rem;\n        .h1 {\n          font-size: 4rem;\n        }\n      }\n      .hero_actions {\n        display: inline-block;\n        .margin(top, medium);\n        > .list {\n          .list-space(medium)\n        }\n        .btn {\n          .btn-text();\n          .btn-big();\n          .btn-none-x();\n        }\n      }\n      // variants\n      .hero--big {\n        .hero_text {\n          .h1 {\n            font-size: 6rem;\n            @media @min-sm {\n              font-size: 9rem;\n            }\n          }\n        }\n      }\n      .hero--center {\n        .hero_content_outer {\n          .align-center();\n        }\n      }\n      .hero--middle {\n        @media @min-sm {\n          .hero_content_outer {\n            align-items: center;\n          }\n        }\n      }\n      .hero--left {\n        @media @min-sm {\n          .hero_content_outer {\n            background: linear-gradient(90deg, fade(@black, 20%) 0%, fade(@black, 0%) 100%);\n          }\n          .hero_content_inner {\n            margin-right: auto;\n            max-width: 55rem;\n          }\n        }\n      }\n      .hero--right {\n        @media @min-sm {\n          .hero_content_outer {\n            .align-right();\n            background: linear-gradient(-90deg, fade(@black, 20%) 0%, fade(@black, 0%) 100%);\n          }\n          .hero_content_inner {\n            margin-left: auto;\n            max-width: 55rem;\n          }\n        }\n      }\n      // line\n      .hero_line {\n        .container-space();\n        pointer-events: none;\n        position: absolute;\n        z-index: 1;\n        top: 0;\n        left: 0;\n        bottom: 0;\n        right: 0;\n      }\n      .hero_line_inner {\n        position: relative;\n        width: 100%;\n        height: 100%;\n      }\n      .hero_line_small {\n        position: absolute;\n        bottom: 0;\n        right: 0;\n        height: 200px;\n        width: 1px;\n        background: @white;\n      }\n      .hero_line_big {\n        position: absolute;\n        margin-right: -1px;\n        bottom: 80px;\n        right: 0;\n        height: 120px;\n        width: 3px;\n        background: @white;\n      }\n      // .slider-navigation-sides\n      .slider-navigation-sides {\n        color: @white;\n      }\n    }\n\n  }\n  //##END\n}\n";