export default "#iframe--furniture-listing-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    /**\n      * .block-listing\n      */\n\n    .block-listing_inner {\n      > .row {\n        .row-space(small);\n        .row-space(medium, sm);\n      }\n    }\n\n    .block-listing_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .block-listing_col--big {\n      .col(12, sm);\n      .col(12, lg);\n    }\n\n    .block-listing_item {\n      .full();\n      .text-default() !important;\n      // card\n      .card-white();\n      .card-small();\n      .card-squared();\n      .card-none-x();\n      .card-title {\n        .font-sans-semibold();\n        .line-height-small();\n        font-size: 1.8rem;\n      }\n      .card-asset {\n        .media-container {\n          .padding(bottom, 75%);\n          background: @shade-100;\n        }\n        .media {\n          .media-cover();\n        }\n      }\n    }\n\n    .block-listing_col--big {\n      .card-asset {\n        .media-container {\n          @media @min-sm {\n            .padding(bottom, 37.5%);\n          }\n          @media @min-lg {\n            .padding(bottom, 25%);\n          }\n        }\n      }\n    }\n\n    .block-listing_content {\n      .padding(y, medium);\n    }\n\n    .block-listing_price {\n      .font-sans-semibold();\n      .line-height-small();\n      font-size: 1.4rem;\n      letter-spacing: 0.1em;\n      text-transform: none;\n    }\n\n  }\n  //##END\n}\n";