export default "#iframe--stores-listing-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-out-quint;\n\n    /**\n      * .block-listing\n      */\n\n    .block-listing_inner {\n      > .row {\n        .row-default();\n        .row-stretch();\n      }\n    }\n\n    .block-listing_col {\n      .col(12);\n      .col(6, sm);\n      .col(4, lg);\n    }\n\n    .block-listing_item {\n      .full();\n      .text-default() !important;\n      .btn-close {\n        .btn-squared();\n        .btn-primary();\n      }\n    }\n\n    .card-asset {\n      text-align: center;\n      .media-container {\n        .padding(bottom, 50%);\n      }\n      .media {\n        .media-cover();\n      }\n    }\n\n    .block-listing_subtitle {\n      .spacing-medium();\n      .font-sans-semibold();\n      font-size: 2rem;\n      line-height: 1.2;\n      letter-spacing: 0;\n      text-transform: none;\n    }\n\n    .block-listing_address {\n      .full();\n      font-size: 1.5rem;\n      line-height: 1.8;\n    }\n\n    .block-listing_additional {\n      .full();\n      font-size: 1.4rem;\n      line-height: 1.8;\n    }\n\n    .block-listing_btns {\n      .list-block();\n      .list-space(tiny);\n      margin-top: auto;\n      .padding(top, medium);\n      .btn {\n        .full();\n        .align-center();\n        .btn-squared();\n      }\n    }\n\n    // front and back\n\n    .block-listing_front {\n      position: relative;\n      z-index: 2;\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      // card\n      .card-squared();\n      .card-small();\n      .text-default();\n      .card-design {\n        border-color: @shade-100;\n        background: @shade-100;\n      }\n      .card-block {\n        .flex-full();\n      }\n      .block-listing_btns {\n        margin-top: auto;\n      }\n    }\n\n    .block-listing_back {\n      position: absolute;\n      z-index: 1;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      transform: rotateY(180deg);\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n      // card\n      .card-squared();\n      .card-small();\n      .text-default();\n      .card-design {\n        border-color: @shade-100;\n        background: @shade-100;\n      }\n    }\n\n    .block-listing_item {\n      perspective: 1400px;\n      .block-listing_item_inner {\n        height: 100%;\n        transform-style: preserve-3d;\n        transition-duration: @time-big;\n        transition-delay: initial;\n        transition-timing-function: @ease-out;\n      }\n      &.active {\n        .block-listing_item_inner {\n          transform: rotateY(180deg);\n          transition-duration: @time-big;\n          transition-delay: initial;\n          transition-timing-function: @ease-in;\n        }\n        .block-listing_back {\n          z-index: 3;\n        }\n      }\n    }\n\n  }\n  //##END\n}\n";