export default "#iframe--furniture-featured-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    /**\n      * .parallax\n      */\n\n    .parallax {\n      .padding(y, huge);\n      background: @white;\n    }\n\n    .parallax_item {\n      .full();\n      .margin(y, huge);\n      .text-default() !important;\n    }\n\n    .parallax--inverse {\n      background: @shade-700;\n      .parallax_item {\n        .text-inverse() !important;\n      }\n    }\n\n    .parallax_item_inner {\n      > .row {\n        align-items: center;\n      }\n    }\n\n    .parallax_img_col {\n      z-index: 1;\n      .col(12);\n      .col-custom(40%, sm);\n    }\n\n    .parallax_item--big {\n      .parallax_img_col {\n        .col-custom(55%, sm);\n      }\n    }\n\n    .parallax_content_col {\n      z-index: 2;\n      .col(12);\n      .col-auto(sm);\n    }\n\n    .parallax_img {\n      .media-container();\n      padding-bottom: 72.6%;\n      overflow: hidden;\n    }\n\n    .parallax_img--vertical {\n      padding-bottom: 121%;\n    }\n\n    .parallax_img_inner {\n      .media-inner();\n      img {\n        .media();\n        .media-cover();\n      }\n    }\n\n    .parallax_content {\n      .padding(x, giant);\n    }\n\n    .parallax_item--align-right {\n      .parallax_content {\n        text-align: right;\n      }\n    }\n\n    .parallax_title {\n      .margin-remove(left, 12rem);\n      .spacing-medium();\n      font-size: 4rem;\n    }\n\n    .parallax_desc {\n      font-size: 1.6rem;\n      line-height: 2.5;\n    }\n\n    .parallax_item--desc-right {\n      .parallax_desc {\n        .padding(left, 6rem);\n        .margin-remove(right, 18rem);\n      }\n    }\n\n    .parallax_item--right {\n      .parallax_img_col {\n        .order(2, sm);\n      }\n      .parallax_content_col {\n        .order(1, sm);\n      }\n      .parallax_title {\n        .margin(left, 0);\n        .margin-remove(right, 12rem);\n      }\n    }\n\n    /**\n      * .btn--line\n      */\n\n    @line-width: 15px;\n    @line-space: 15px;\n    @line-opacity: 0.4;\n\n    .btn--line {\n      .padding(x, none);\n      .font-sans-semibold();\n      font-size: 12px;\n      letter-spacing: .1em;\n      text-transform: uppercase;\n      border-color: transparent;\n      background: transparent;\n      color: @text;\n      &:hover, &.hover {\n        color: @text;\n        box-shadow: none;\n      }\n      &:active, &.active {\n        border-color: transparent;\n        background: transparent;\n        box-shadow: none;\n        transform: none;\n      }\n    }\n\n    .btn--line_text {\n      .padding(left, @line-width + @line-space);\n    }\n\n    .btn--line_line {\n      position: absolute;\n      width: @line-width;\n      height: 1px;\n      background: @text;\n      opacity: @line-opacity;\n    }\n\n    .parallax--inverse {\n      .btn--line {\n        color: @text-inverse;\n        &:hover, &.hover {\n          color: @text-inverse;\n          box-shadow: none;\n        }\n      }\n      .btn--line_line {\n        background: @white;\n      }\n    }\n\n  }\n  //##END\n}\n";