export default "#iframe--collapse-theme-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    /**\n      * vars\n      */\n\n    // ease\n\n    @ease-in: @ease-out-quint;\n    @ease-out: @ease-in-out-quint;\n\n    /**\n      * drop\n      */\n\n    .drop-default {\n      > .drop-inner {\n        box-shadow: @shadow-light;\n        > .drop-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    .drop-primary {\n      > .drop-inner {\n        box-shadow: @shadow-dark;\n        > .drop-design {\n          box-shadow: none;\n        }\n      }\n    }\n\n    // animation\n\n    .drop-container {\n      > .drop {\n        > .drop-inner {\n          .collapse-height();\n        transition: height @time-medium @ease-in;\n          > .drop-design {\n            opacity: 0;\n            transform: translateY(-1rem);\n          }\n          > .drop-content {\n            opacity: 0;\n            transform: translateY(-1.5rem);\n          }\n        }\n      }\n      &.in {\n        animation-duration: @time-medium; // needed for animation time and backdrop animation time, put max animation time\n        > .drop {\n          > .drop-inner {\n            transition: height @time-medium @ease-in;\n            > .drop-design {\n              opacity: 1;\n              transform: none;\n              transition: height @time-medium @ease-in, transform @time-medium @ease-in, opacity @time-tiny @ease-in;\n            }\n            > .drop-content {\n              opacity: 1;\n              transform: none;\n              transition: transform @time-medium @ease-in, opacity @time-medium @ease-in;\n            }\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-small; // needed for animation time and backdrop animation time, put max animation time\n        > .drop {\n          > .drop-inner {\n            transition: height @time-small @ease-out;\n            > .drop-design {\n              opacity: 0;\n              transform: translateY(1rem);\n              transition: height @time-small @ease-out, transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n            }\n            > .drop-content {\n              opacity: 0;\n              transform: translateY(1.5rem);\n              transition: transform @time-small @ease-out, opacity @time-small @ease-out;\n            }\n          }\n        }\n      }\n    }\n\n    /**\n      * overlay\n      */\n\n    .overlay {\n      .card-item--menu {\n        background: @accent;\n      }\n    }\n\n    // animation\n\n    .overlay {\n      .collapse-height();\n      .overlay-design {\n        opacity: 0;\n        transform: translateY(-2rem);\n      }\n      .card-overlay {\n        > .btn-close {\n          opacity: 0;\n        }\n        .card-block, .card-content {\n          opacity: 0;\n          transform: translateY(-2.5rem);\n        }\n      }\n      &.in {\n        animation-duration: @time-medium; // needed for animation time and backdrop animation time, put max animation time\n        transition: height @time-medium @ease-in;\n        .overlay-design {\n          opacity: 1;\n          transform: none;\n          transition: transform @time-medium @ease-in, opacity @time-tiny @ease-in;\n        }\n        .card-overlay {\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.in();\n            animation-delay: @time-medium;\n            animation-fill-mode: forwards;\n            opacity: 0;\n          }\n          .card-block, .card-content {\n            opacity: 1;\n            transform: none;\n            transition: transform @time-medium @ease-in, opacity @time-tiny @ease-in;\n          }\n        }\n      }\n      &.out {\n        animation-duration: @time-small; // needed for animation time and backdrop animation time, put max animation time\n        transition: height @time-small @ease-out;\n        .overlay-design {\n          opacity: 0;\n          transform: translateY(2rem);\n          transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n        }\n        .card-overlay {\n          > .btn-close {\n            // use animation to not override .btn transitions\n            .anim-zoom.out();\n            animation-delay: initial;\n          }\n          .card-block, .card-content {\n            opacity: 0;\n            transform: translateY(2.5rem);\n            transition: transform @time-small @ease-out, opacity @time-tiny @ease-out @time-tiny;\n          }\n        }\n      }\n    }\n\n    /**\n      * group\n      */\n\n    .group {\n      .collapse-width();\n      &.in {\n        transition: width @time-medium @ease-out;\n      }\n      &.out {\n        transition: width @time-medium @ease-out;\n      }\n    }\n\n  }\n  //##END\n}\n";