export default "#iframe--fashion-listing-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    /**\n      * .block-listing\n      */\n\n    .block-listing_inner {\n      > .row {\n        .row-space(large);\n      }\n    }\n\n    .block-listing_col {\n      .col(12);\n      .col(6, sm);\n      .col(3, lg);\n    }\n\n    .block-listing_col--big {\n      .col(12, sm);\n      .col(12, lg);\n    }\n\n    .block-listing_item {\n      .full();\n      // card\n      .card-white();\n      .card-small();\n      .card-squared();\n      .card-none-x();\n      text-align: center;\n      .card-title {\n        .font-sans-semibold();\n        font-size: 2rem;\n      }\n      .card-asset {\n        .media-container {\n          .padding(bottom, 150%);\n        }\n        .media {\n          .media-cover();\n        }\n      }\n    }\n\n    .block-listing_col--big {\n      .card-asset {\n        .media-container {\n          @media @min-sm {\n            .padding(bottom, 37.5%);\n          }\n          @media @min-lg {\n            .padding(bottom, 25%);\n          }\n        }\n      }\n    }\n\n    /**\n      * .media_mask\n      */\n\n    .media_mask {\n      position: absolute;\n      z-index: @index-active + 1;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 0;\n      background: @white;\n    }\n\n  }\n  //##END\n}\n";