export default "/**\n * overlay\n */\n\n.overlay {\n  .toggle-flex();\n  position: fixed;\n  z-index: @index-overlay;\n  top: 0;\n  left: 0;\n  bottom: 0; // @FIX no height or it bugs collapse animation\n  right: 0; // @FIX no width or it bugs collapse animation\n  // scroll\n  overflow: hidden;\n}\n\n.overlay-container {\n  display: flex;\n  flex-wrap: nowrap;\n  flex-direction: column;\n  justify-content: center;\n  margin: auto; // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container\n  width: 100%;\n  min-height: 100vh;\n}\n\n.overlay-inner {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  align-self: center;\n  position: relative;\n  z-index: @index-backdrop + 1;\n  width: 100%;\n  border-radius: inherit;\n}\n\n.overlay-design {\n  .design-setup();\n  border-radius: inherit;\n}\n\n// preset\n\n.overlay-screen {\n  .overlay-container {\n    position: relative;\n    z-index: @index-overlay;\n    max-width: none !important;\n    .padding(all, none) !important;\n    .overlay-inner {\n      position: static;\n    }\n  }\n  .overlay-inner {\n    .flex-auto();\n    display: flex;\n    flex-direction: column;\n    max-width: none !important;\n  }\n  .card-overlay .card-inner {\n    align-items: center;\n  }\n}\n\n[class^='overlay-position-'], [class*=' overlay-position-'] {\n  .overlay-container {\n    position: relative;\n    z-index: @index-overlay;\n    .padding(all, none) !important;\n    .overlay-inner {\n      position: static;\n    }\n  }\n  .overlay-inner {\n    .flex-auto();\n    display: flex;\n    flex-direction: column;\n  }\n}\n\n.overlay-position-left {\n  .overlay-container {\n    margin-left: 0;\n    margin-right: auto;\n  }\n}\n\n.overlay-position-right {\n  .overlay-container {\n    margin-left: auto;\n    margin-right: 0;\n  }\n}\n\n// special\n\n.overlay-disable {\n  .xt-disable();\n  display: flex !important;\n  overflow: initial !important;\n  html.xt-scrollbar & {\n    overflow: initial !important;\n  }\n  position: static;\n  z-index: initial;\n  top: auto;\n  left: auto;\n  bottom: auto;\n  right: auto;\n  width: auto;\n  height: auto;\n  .overlay-container {\n    z-index: initial;\n    padding: 0;\n    width: 100%;\n    min-height: auto;\n    .overlay-inner {\n      z-index: initial;\n      .overlay-design {\n        display: none;\n      }\n      .card-overlay {\n        .card-disable();\n      }\n      .btn-close {\n        display: none !important;\n      }\n    }\n    .overlay-inner, .card-overlay .card-content {\n      max-width: none;\n    }\n  }\n  > .backdrop {\n    display: none !important;\n  }\n}\n\n// special\n\n.overlay-close-fixed {\n  .btn-close {\n    position: fixed !important;\n    z-index: @index-btn-close;\n    line-height: 1;\n  }\n}\n";