export default "/**\n * card interactive\n */\n\n// variant\n\na.card-default, label.card-default {\n  &:hover, &.hover {\n    color: @text;\n    .card-design {\n      border-color: @shade-100;\n      background: @shade-100;\n    }\n  }\n  &:active, &.active {\n    color: @text;\n    .card-design {\n      border-color: @shade-200;\n      background: @shade-200;\n    }\n  }\n}\n\na.card-primary, label.card-primary {\n  &:hover, &.hover {\n    color: @text-inverse;\n    .card-design {\n      border-color: @accent;\n      background: @accent;\n    }\n  }\n  &:active, &.active {\n    color: @text-inverse;\n    .card-design {\n      border-color: @accent-dark;\n      background: @accent-dark;\n    }\n  }\n}\n\n// animation\n\na.card, label.card {\n  .card-design {\n    transition: all @time-small @ease-out;\n  }\n  &:hover, &.hover {\n    .card-design {\n      transition: all @time-small @ease-in;\n    }\n  }\n  &:active, &.active {\n    .card-design {\n      transition: all @time-small @ease-in;\n    }\n  }\n}\n";