export default "@import 'xtend-library/src/core/structure/_vars.less';\n@import 'xtend-library/src/core/structure/_util.less';\n\n/**\n * background\n */\n\nhtml {\n  .overflow-style();\n}\n\nbody {\n  background: @white;\n  overflow-x: hidden !important;\n  overflow-y: scroll !important;\n  .overflow-style();\n}\n\n/**\n * scrollbar\n */\n\n.overflow-style(@mode: none) {\n  -webkit-overflow-scrolling: touch;\n  & when (@mode = none) {\n    // webkit\n    &::-webkit-scrollbar {\n      height: 8px;\n      width: 8px;\n      background: @shade-100;\n      @media (hover: none) {\n        display: none;\n      }\n    }\n    &::-webkit-scrollbar-thumb {\n      background: fade(@shade-700, 40%);\n    }\n    // firefox\n    scrollbar-width: thin;\n    scrollbar-color: fade(@shade-700, 40%) transparent;\n  }\n  & when (@mode = inner) {\n    // webkit\n    &::-webkit-scrollbar {\n      height: 4px;\n      width: 4px;\n      background: transparent;\n    }\n    &::-webkit-scrollbar-thumb {\n      background: fade(@shade-700, 40%);\n    }\n    // firefox\n    scrollbar-color: fade(@shade-700, 40%) transparent;\n  }\n}\n\n/**\n * selection\n */\n\n::selection {\n  background-color: @accent-dark !important;\n  color: @white !important;\n}\n\n/**\n * focus\n * to disable focus add html.xt-focus-disable class\n */\n\nhtml.xt-focus-visible:not(.xt-focus-disable) {\n  :focus {\n    outline-color: rgb(77, 144, 254);\n  }\n  // custom focus on forms\n  input:not([type=\"checkbox\"]):not([type=\"radio\"]), select, textarea {\n    &:focus {\n      outline: none;\n    }\n  }\n  /*\n  // custom focus for checkbox and radio\n  input[type=\"checkbox\"], input[type=\"radio\"] {\n    &:focus, &:focus ~ .form-label:before {\n      outline-color: rgb(77, 144, 254);\n      outline-offset: -2px;\n      outline-style: auto;\n      outline-width: 5px;\n    }\n  }\n  */\n}\n\n/**\n * disabled\n */\n\n[disabled], .disabled {\n  cursor: not-allowed;\n  pointer-events: none;\n  // when not .active also graphic changes\n  &:not(.active) {\n    opacity: .75;\n  }\n}\n";