export default "@import 'xtend-library/src/addons/card/_separator.less';\n\n/**\n * card-separator\n */\n\n.card {\n  &.card-separator {\n    .card-item + .card-item,\n    .card-item + .card-asset,\n    .card-item + .card-content,\n    .card-asset + .card-asset,\n    .card-asset + .card-item,\n    .card-asset + .card-content,\n    .card-content + .card-content,\n    .card-content + .card-item,\n    .card-content + .card-asset {\n      &:before {\n        content: '';\n        z-index: @index-active + 1;\n        position: absolute;\n        top: 0;\n        bottom: auto;\n        left: 0;\n        right: 0;\n        width: auto;\n        height: 1px;\n      }\n    }\n  }\n}\n\n.card-separator-vertical {\n  .card-item + .card-item,\n  .card-asset + .card-item,\n  .card-item + .card-asset {\n    &:before {\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: auto;\n      width: 1px;\n      height: auto;\n    }\n  }\n}\n\n// variant\n\n.card-default {\n  &.card-separator {\n    .card-item + .card-item,\n    .card-item + .card-asset,\n    .card-item + .card-content,\n    .card-asset + .card-asset,\n    .card-asset + .card-item,\n    .card-asset + .card-content,\n    .card-content + .card-content,\n    .card-content + .card-item,\n    .card-content + .card-asset {\n      &:before {\n        background: fade(@shade-700, 10%);\n      }\n    }\n  }\n}\n\n.card-primary {\n  &.card-separator {\n    .card-item + .card-item,\n    .card-item + .card-asset,\n    .card-item + .card-content,\n    .card-asset + .card-asset,\n    .card-asset + .card-item,\n    .card-asset + .card-content,\n    .card-content + .card-content,\n    .card-content + .card-item,\n    .card-content + .card-asset {\n      &:before {\n        background: fade(@shade-100, 10%);\n      }\n    }\n  }\n}\n\n// special\n\n.card-horizontal {\n  &.card-separator {\n    .card-separator-vertical();\n  }\n}\n";