export default "/**\n * sidebar\n */\n\n// .gatsby_site_article_sidebar\n\n@gatsby_site_article_sidebar_width: 30rem;\n\n.gatsby_site_article_sidebar {\n  position: fixed;\n  z-index: 2;\n  top: 0;\n  left: 0;\n  bottom: 0;\n  background: linear-gradient(-90deg, @grad-accent);\n  color: @white;\n  @media @max-md {\n    .sticky-disable();\n  }\n  @media @min-md {\n    width: @gatsby_site_article_sidebar_width;\n  }\n  overflow-x: hidden;\n  overflow-y: scroll;\n  .overflow-style(@mode: inner) !important;\n  line-height: 1;\n}\n\n/**\n * header\n */\n\n// .gatsby_site_header\n\n.gatsby_site_header {\n  .padding(top, 3rem);\n  .padding(bottom, 1rem);\n  .padding(x, 3rem);\n  color: @white;\n}\n\n.gatsby_site_header_logo_outer {\n  .list();\n  .list-space(small);\n  align-items: baseline;\n  justify-content: space-between;\n}\n\n.gatsby_site_header_top_social {\n  .list();\n  .list-space(tiny);\n}\n\n.gatsby_btn-site_header_top_social {\n  .btn-nodesign();\n  padding: 0;\n  border-radius: 0;\n  color: @text-inverse;\n  line-height: 1;\n  text-transform: uppercase;\n  opacity: 0.8;\n  &:hover, &.hover, &:active, &.active {\n    color: @text-inverse;\n    opacity: 1;\n  }\n  &:active, &.active {\n    transform: scale(1.2);\n  }\n}\n\n// .gatsby_site_header_listing\n\n.gatsby_site_header_listing {\n  .padding(bottom, 3rem);\n  .padding(x, 3rem);\n}\n\n.gatsby_site_header_cat {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: flex-start;\n}\n\n.gatsby_site_header_inner, .gatsby_site_header_cat, .gatsby_site_header_item, .gatsby_site_header_adiacent, .gatsby_site_header_adiacent_inner {\n  position: relative;\n}\n\n.gatsby_site_header_sub {\n  .full();\n}\n\n.gatsby_site_header_adiacent, .gatsby_site_header_adiacent_inner {\n  .full();\n  display: none;\n  &.active {\n    display: block;\n  }\n}\n\n.gatsby_cat--site_article_sidebar {\n  .h6();\n  .margin(top, medium) !important;\n  .margin(bottom, tiny) !important;\n  color: fade(@white, 80%);\n}\n\n.gatsby_site_header_links_container {\n  .margin(top, medium);\n  .padding(y, small);\n  border-top: 1px solid fade(@shade-700, 20%);\n  border-bottom: 1px solid fade(@shade-700, 20%);\n}\n\n.gatsby_site_header_links {\n  margin-top: 1px;\n  margin-bottom: 1px;\n  .margin-remove(x, .8rem);\n}\n\n.gatsby_site_header_sub {\n  .margin-remove(x, .8rem);\n  .gatsby_site_header_item {\n    position: relative;\n  }\n}\n\n.gatsby_site_header_adiacent {\n  .gatsby_site_header_item {\n    position: relative;\n    .padding(left, .8rem);\n  }\n}\n\n.gatsby_site_header_adiacent_inner {\n  .gatsby_site_header_item {\n    position: relative;\n    .padding(left, .8rem);\n  }\n}\n\n.gatsby_btn-site_article_sidebar,\n.gatsby_btn-site_header_link {\n  pointer-events: auto !important;\n  .full();\n  .btn-none();\n  box-shadow: none !important;\n  margin-bottom: 1px;\n  .font-sans-regular();\n  font-size: 1.2rem;\n  color: fade(@text-inverse, 60%);\n  line-height: 1;\n  text-transform: none;\n  letter-spacing: .025em;\n  // custom\n  > span {\n    border-radius: 6px;\n    padding: .4rem .7rem;\n  }\n  &.gatsby_btn-site_header_link {\n    > span {\n      .full();\n      padding: .5rem .8rem;\n    }\n    .font-sans-semibold();\n    text-transform: uppercase;\n    letter-spacing: .05em;\n  }\n  &.gatsby_btn-site_article_sidebar--sub {\n    > span {\n      .full();\n    }\n  }\n  &.gatsby_btn-site_article_sidebar--adiacent {\n    > span {\n      background: fade(@shade-700, 20%);\n    }\n  }\n  &.gatsby_btn-site_article_sidebar--adiacent_inner {\n    font-size: 10px;\n    text-transform: uppercase;\n    letter-spacing: .05em;\n  }\n  &:hover, &.hover {\n    > span {\n      border-color: transparent;\n      background: fade(@shade-700, 20%);\n      color: @text-inverse;\n      transform: translateX(.25rem);\n    }\n  }\n  &:active, &.active, &.current {\n    > span {\n      border-color: transparent;\n      background: fade(@shade-700, 40%);\n      color: @text-inverse;\n      transform: none;\n    }\n  }\n  // animation\n  transition: all @time-tiny @ease-out;\n  > span {\n    transition: all @time-tiny @ease-out;\n  }\n  &:hover, &.hover {\n    transition: all @time-tiny @ease-in;\n    > span {\n      transition: all @time-tiny @ease-in;\n    }\n  }\n  &:active, &.active {\n    transition: all @time-tiny @ease-in;\n    > span {\n      transition: all @time-tiny @ease-in;\n    }\n  }\n}\n";