export default "@import 'xtend-library/src/extensions/loader/loader.less';\n\n/**\n * .infinite-scroll\n */\n\n.infinite-scroll {\n  .margin(top, big);\n  > .list-block {\n    .list-space(tiny);\n    align-items: center;\n  }\n}\n\n.infinite-scroll--more {\n  .font-sans-semibold();\n  font-size: 12px;\n  letter-spacing: .05em;\n}\n\n.infinite-scroll--spinner {\n  .loader-animated.out();\n}\n.infinite-scroll-loading {\n  .infinite-scroll--spinner {\n    .loader-animated.in();\n  }\n}\n\n[data-xt-infinite-scroll-trigger] {\n  .content-nomore {\n    display: none;\n  }\n  .content {\n    transition: transform @time-small @ease-out-expo, opacity @time-small @ease-out-expo;\n  }\n}\n\n.infinite-scroll-loading {\n  [data-xt-infinite-scroll-trigger] {\n    pointer-events: none;\n    > .content {\n      opacity: 0;\n      transform: translateY(1rem);\n      transition: transform @time-small @ease-out-expo, opacity @time-small @ease-out-expo;\n    }\n  }\n}\n\n.infinite-scroll-nomore {\n  [data-xt-infinite-scroll-trigger] {\n    .disabled();\n    pointer-events: none;\n    .content {\n      display: none;\n    }\n    .content-nomore {\n      display: block;\n    }\n  }\n}";