export default ".demo--scroll-responsive {\n  .card {\n    @media @max-sm {\n      // mode-once\n      &:not(.scroll-done) {\n        opacity: 0;\n        &.fade {\n          .anim-x.in();\n        }\n        &.fade-out {\n          .anim-x.out();\n        }\n      }\n    }\n    @media @min-sm {\n      // mode-infinite\n      opacity: 0;\n      &.fade {\n        .anim-x.in();\n      }\n      &.fade-out {\n        .anim-x.out();\n      }\n    }\n  }\n}\n";