export default "#iframe--fashion-hero-v1 {\n  //##START\n  body { // add your own selector instead of body to contain the code\n\n    @import 'xtend-library/src/addons/slider/navigation-sides.less';\n\n    /**\n      * slider\n      */\n\n    .slider {\n      background: @black;\n      .slides-inner {\n        .row-space(none);\n        // dragger animations\n        transition: transform @time-large @ease-out-quart;\n      }\n      .slide {\n        .col(12);\n      }\n      .slider-navigation {\n        color: @white;\n      }\n      .slider-pagination {\n        display: none;\n      }\n      // .hero\n      .hero {\n        .full();\n        display: block;\n        overflow: hidden;\n      }\n      .hero_asset {\n        position: relative;\n        overflow: hidden;\n        width: 100%;\n        height: 100vh;\n        height: calc(var(--vh, 1vh) * 100);\n      }\n      .hero_content_outer {\n        position: absolute;\n        z-index: 3;\n        top: 0;\n        left: 0;\n        bottom: 0;\n        right: 0;\n        display: flex;\n        align-items: flex-end;\n      }\n      .hero_content {\n        .container();\n      }\n      .hero_content_inner {\n        .full();\n        .text-inverse();\n        .padding(y, giant);\n        max-width: 70rem;\n      }\n      .hero_text {\n        font-size: 1.7rem;\n        .h1 {\n          font-size: 4rem;\n        }\n      }\n      .hero_actions {\n        display: inline-block;\n        .margin(top, medium);\n        > .list {\n          .list-space(medium)\n        }\n        .btn {\n          .btn-text();\n          .btn-big();\n          .btn-none-x();\n        }\n      }\n      // variants\n      .hero--big {\n        .hero_text {\n          .h1 {\n            font-size: 6rem;\n            @media @min-sm {\n              font-size: 9rem;\n            }\n          }\n        }\n      }\n      .hero--center {\n        .hero_content_outer {\n          .align-center();\n        }\n      }\n      .hero--middle {\n        @media @min-sm {\n          .hero_content_outer {\n            align-items: center;\n          }\n        }\n      }\n      .hero--left {\n        @media @min-sm {\n          .hero_content_outer {\n            background: linear-gradient(90deg, fade(@black, 20%) 0%, fade(@black, 0%) 100%);\n          }\n          .hero_content_inner {\n            margin-right: auto;\n            max-width: 55rem;\n          }\n        }\n      }\n      .hero--right {\n        @media @min-sm {\n          .hero_content_outer {\n            .align-right();\n            background: linear-gradient(-90deg, fade(@black, 20%) 0%, fade(@black, 0%) 100%);\n          }\n          .hero_content_inner {\n            margin-left: auto;\n            max-width: 55rem;\n          }\n        }\n      }\n      // .slider-navigation-sides\n      .slider-navigation-sides {\n        color: @white;\n      }\n    }\n\n  }\n  //##END\n}\n";